
.maincontent
{padding-top: 20px;
    background-image: linear-gradient(to bottom,#654a86,#534292);
    background-color: #534292;

}


.rounded-button
{

    border-radius: 10px;
    padding: 10px;
    font-weight: bolder;
    font-size:20px;
    background: linear-gradient( 19deg, #ABDCFF 10%, #0396FF 100%);
    box-shadow: 3px 3px 0 rgba(82,62,107, 0.2);
    line-height: 20px;
}
.rounded-button:hover{
    box-shadow: 6px 6px 0 rgba(82,62,107, 0.2);
    background: linear-gradient( 335deg, #ABDCFF 10%, #0396FF 100%);
}

.fa.fa-edit {
    color: #18a2b9;
}
.fa.fa-minus-circle {
    color: red;
}

.landing {
    position: relative;
    /* background: url("../img/showcase.jpg") no-repeat; */
    background-size: cover;
    background-position: center;
    height: 100vh;
    margin-top: -24px;
    margin-bottom: -50px;
}

.landing-inner {
    padding-top: 80px;
}

.dark-overlay {
    background-color: rgba(0, 0, 0, 0.7);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.card-form {
    opacity: 0.9;
}

.latest-profiles-img {
    width: 40px;
    height: 40px;
}

.form-control::placeholder {
    color: #bbb !important;
}
