.main-background {
    background: #F8F6F6;
    padding-top: 10px;
}
.main-content
{
    text-align: center;
    z-index: -5;
}

.jumbotron-home
{
    background: url("../images/ppm.jpg" ) fixed bottom;
    background-size: cover;
    position: relative;
    top:0;
    left: 0;
    background-repeat: no-repeat;
    background-position: center;
    width: 105%;
    min-height: 80vh;
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    flex-wrap: wrap;
    z-index: 1;
}
.jumbotron-home::before {
    content: "";
    display: block;
    position: absolute;
    z-index:-1;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(57, 49, 49, 0.8)
}

