.jumbotron-home
{
    background: url(/static/media/ppm.1560adba.jpg ) fixed bottom;
    background-size: cover;
    position: relative;
    top:0;
    left: 0;
    background-repeat: no-repeat;
    background-position: center;
    width: 105%;
    min-height: 80vh;
    display: flex;
    flex-wrap: wrap;
    z-index: 1;
}
.jumbotron-home::before {
    content: "";
    display: block;
    position: absolute;
    z-index:-1;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(57, 49, 49, 0.8)
}


